.shop-introduce {
  margin-bottom: 60px;
  @media screen and (min-width: $desktop-small){
    margin-bottom: 100px;
  }

  .container {
    @media screen and (min-width: $desktop-small){
      display: flex;
    }
  }

  &__item {
    display: flex;
    align-items: center;
    @include backgroundSize(cover);
    min-height: 320px;
    margin-bottom: 20px;
    @media screen and (min-width: $desktop-small){
      width: calc(50% - 10px);
      margin-right: 20px;
      margin-bottom: 0;
      min-height: 350px;
    }

    &:last-child {
      margin-right: 0;
    }

    .content {
      padding: 50px 20px;
      @media screen and (min-width: $tablet-medium){
        width: calc(100% - 300px);
        padding: 50px 32px 50px 50px;
      }

      .category {
        font-size: 16px;
        line-height: 16px;
        color: var(--gray);
        margin-bottom: 9px;
      }

      .title {
        font-weight: bold;
        margin-bottom: 15px;
      }
    }
  }
}
