.footer {
  &__main {
    padding: 50px 0 30px;
    background: var(--white-2);
    @media screen and (min-width: $desktop-small){
      padding: 90px 0 50px;
    }

    .row {
      display: flex;
      flex-wrap: wrap;
      @media screen and (min-width: $tablet-medium){
        .col {
          flex: 1;
          padding: 0 15px;
        }
      }
      @media screen and (max-width: $tablet-medium - 1){
        .col {
          width: 50%;
          margin-bottom: 10px;
        }
      }
    }

    ul {
      li {
        font-weight: 600;
        font-size: 21px;
        line-height: 27px;
        color: var(--dark);

        ul {
          margin-top: 15px;
          @media screen and (min-width: $tablet-medium){
            margin-top: 20px;
          }
        }

        li {
          font-weight: 400;
          font-size: 16px;
          line-height: 21px;
          color: var(--gray);
          margin-bottom: 10px;
          @media screen and (min-width: $tablet-medium){
            margin-bottom: 15px;
          }
          a:hover {
            text-decoration: underline;
          }
        }
      }
    }

    .follow-us {
      margin-top: 15px;
      margin-bottom: 25px;
      font-size: 14px;
      line-height: 20px;
      color: var(--gray);
      @media screen and (min-width: $tablet-medium){
        margin-top: 22px;
      }
    }
  }

  .social {
    a {
      color: var(--gray);
    }
  }

  &__bottom {
    padding: 15px 0;
    background: var(--dark);

    .container {
      color: var(--line-2);
      @media screen and (min-width: $tablet-medium){
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
      @media screen and (max-width: $tablet-medium - 1){
        .copyright {
          font-size: 14px;
          margin-bottom: 10px;
        }
      }
    }

    .pays-list {
      display: flex;

      li {
        margin-right: 8px;
        max-width: 47px;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
}
