.clearfix {
  @include clearfix;
}

body {
  font-family: $font-1; //Default font family
  color: var(--dark); //Default text color
  letter-spacing: normal; //Default letter spacing
  font-size: 16px; //Default font size
  line-height: 1.5; //Default line height
  font-weight: 400; //Default font-weight
  background: var(--white); //Default background
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  @media screen and (min-width: $desktop-medium) {
    max-width: $desktop-medium;
  }

  &-fluid {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    @media screen and (min-width: $desktop-large) {
      max-width: $desktop-large;
    }
  }
}

h1 {
  font-size: 50px;
  line-height: 60px;
  @media screen and (min-width: $desktop-small){
    font-size: 75px;
    line-height: 85px;
  }
}

h2 {
  font-size: 30px;
  line-height: 35px;
  @media screen and (min-width: $tablet-medium){
    font-size: 35px;
    line-height: 40px;
  }
}

h3 {
  font-size: 30px;
  line-height: 35px;
}

h4 {
  font-size: 21px;
  line-height: 25px;
}

h5 {
  font-size: 16px;
}

h6 {
  font-size: 14px;
}

.top-title {
  text-align: center;
  margin-bottom: 30px;
  @media screen and (min-width: $tablet-medium){
    margin-bottom: 40px;
  }
  &.left {
    text-align: left;
  }

  .sub-title {
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    text-transform: uppercase;
    color: var(--gray);
    margin-bottom: 10px;
    @media screen and (min-width: $tablet-medium){
      margin-bottom: 20px;
    }
  }

  .title {
    font-weight: 600;
    font-size: 30px;
    line-height: 35px;
    color: var(--dark);
    @media screen and (min-width: $tablet-medium){
      font-size: 35px;
    }
  }
}

.link-full {
  @include positionFull();
  z-index: 10;
}

.text-center {
  text-align: center;
}

.image-ani {
  overflow: hidden;
  img {
    transition: opacity .3s ease,transform 1.1s cubic-bezier(.10,.8,.5,1) 0s;
  }
  &:hover {
    img {
      transform: scale(1.1);
    }
  }
}
