.product {
  &__item {

    .image {
      position: relative;
      margin-bottom: 10px;
      overflow: hidden;
      height: 380px;
      max-height: 380px;
      border: 1px solid transparent;
      border-radius: 3px;
      @media screen and (min-width: $desktop-small){
        height: 470px;
        max-height: 470px;
      }
      &:hover {
        border-color: var(--black);
        &::after {
          transform: translateY(0);
          opacity: 1;
        }
      }
      &::after {
        content: '\e902';
        @include e-comstore;
        position: absolute;
        right: 15px;
        bottom: 15px;
        transform: translateY(100px);
        opacity: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
        background-color: var(--white);
        transition: all .3s ease-in;
      }
      img {
        @include positionFull();
        object-fit: cover;
      }
    }

    .sale {
      position: absolute;
      left: 15px;
      top: 15px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 12px;
      line-height: 16px;
      text-align: center;
      letter-spacing: 0.2em;
      text-transform: uppercase;
      color: var(--white);
      background-color: var(--red);
      border-radius: 40px;
      height: 25px;
      width: 60px;
      z-index: 1;
    }

    .title {
      font-size: 18px;
      line-height: 18px;
      min-height: 18px;
      color: var(--dark);
      margin-bottom: 12px;
      a {
        position: relative;
        padding-bottom: 2px;
        &:hover {
          &::after {
            right: 0;
          }
        }

        &::after {
          content: '';
          position: absolute;
          left: 0;
          right: 100%;
          bottom: 0;
          height: 1px;
          background-color: var(--dark);
          transition: all .3s ease;
        }
      }
    }

    .price {
      font-size: 18px;
      line-height: 18px;
      min-height: 18px;
      color: var(--red);
    }

    .old-price {
      font-size: 16px;
      line-height: 16px;
      color: var(--l-gray);
      margin-left: 7px;
    }
  }
}

